.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header-inner {
  background: #121212;
  padding: 4rem;
  border: 4px solid #fff;
}

@media (max-width: 568px) {
  .header-inner {
    padding: 0.5rem;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  margin: 1rem 0 0 0;
  line-height: 1.2;
}
h2 {
  margin: 1rem 0 0 0;
  line-height: 1.2;
}
h5 {
  margin: 1rem 0 0 0;
  line-height: 1.2;
  color: #61dafb;
}
h5:nth-of-type(1) {
  color: #fff;
}

.container {
  display: flex;
  padding: 1rem;
}

.container .info,
.container .business {
  margin-top: 1rem;
  flex: 0 0 50%;
  display: table;
}

.container .row {
  display: table-row;
}

.container .row .td {
  display: table-cell;
  text-align: left;
  font-size: 16px;
}
@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }
  .container .info .row .td {
    font-size: 14px;
  }
}
.container .row .td.title,
.title {
  text-align: left;
  font-size: 16px;
  color: orange;
  text-transform: uppercase;
  font-weight: 600;
}
.container .row .td.second {
  padding-left: 0.5rem;
}

a,
a:link,
a:active {
  text-decoration: none;
  color: #fff;
}
